.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

.text-overflow-pf {
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  word-wrap: normal; }

.alert {
  border-width: 1px;
  padding-left: 47px;
  padding-right: 14px;
  position: relative;
  word-wrap: break-word; }

.alert .alert-link {
  color: #0088ce; }

.alert .alert-link:hover {
  color: #00659c; }

.alert > .btn.pull-right {
  margin-top: -3px; }

.alert > .pficon {
  font-size: 22px;
  position: absolute;
  left: 13px;
  top: 10px; }

.alert .close {
  opacity: .85; }

.alert .close:focus, .alert .close:hover {
  opacity: 1; }

.alert .pficon-info {
  color: #4d5258; }

.alert-dismissable {
  padding-right: 28px; }

.alert-dismissable .close {
  right: -13px;
